// About Page
.main-about-page {
  width: 100%;

  h2 {
    font-size: 26px;
    margin-bottom: .5rem;
    padding-top: 10px;
  }

  p {
    margin-bottom: .5rem;
  }
}

@media (min-width: 430px) {
  .main-about-page {
    padding: 0 15px;
  }
}

@media (min-width: 520px) {
  .main-about-page {
    padding: 0 20px;
  }
}

@media (min-width: 820px) {
  .main-about-page {
    margin: 0 auto 10px;
    max-width: 780px;
  }
}

.profile-img {
  border-radius: 110px;
  display: block;
  margin: 0 auto 20px;
  max-width: 220px;
}

.header-about-page {
  margin-bottom: 25px;
  text-align: center;

  h1 {
    font-size: 2.2rem;
  }

  hr {
    border: 0;
    border-top: $border;
    width: 340px;
  }

  ul {
    display: inline-flex;

    li {
      margin: 3px;
    }

    a {
      color: $text-color;
      font-size: 1.6rem;
      padding: .5rem .7rem;
    }

    a:hover {
      color: $principal-color;
      text-decoration: none;
    }
  }
}

.skill-list {
  ul {
    margin-left: 25px;
  }

  li {
    display: flex;
    padding: 0 0 .5rem 1.3px;

    i {
      padding-right: 10px;
    }
  }
}

.section-separator {
  border: 0;
  border-top: $border;
  margin-top: 30px;
}
