// Reset
html {
  background: #ffffff;
  color: #000000;
}

body,
div,
dl, dt, dd,
ul, ol, li,
h1, h2, h3, h4, h5, h6,
pre,
code,
legend,
p,
blockquote,
th, td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

img {
  border: 0;
}

address,
caption,
cite,
code,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ol,
ul {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: normal;
}

q {
  ::before, ::after {
    content: '';
  }
}

abbr {
  border: 0;
  font-variant: normal;
}

code {
  font-family: monospace, monospace;
  font-size: 1em;
}
